import Vue from 'vue'
import http from './http'

// 根据百分比生成颜色
Vue.prototype.$dynamicColor = (num) => "rgb(" + Math.round(255 * (1 - num)) + ", " + Math.round(255 * num) + ", 0)";
// 格式化时间戳
Vue.prototype.$dateFormat = (date, format = 'yyyy-MM-dd hh:mm:ss') => {
    date = new Date(date); //新建日期对象
    /*日期字典*/
    var map = {
        M: date.getMonth() + 1, //月份
        d: date.getDate(), //日
        h: date.getHours(), //小时
        m: date.getMinutes(), //分
        s: date.getSeconds(), //秒
        q: Math.floor((date.getMonth() + 3) / 3), //季度
        S: date.getMilliseconds() //毫秒
    };
    /*正则替换*/
    format = format.replace(/([yMdhmsqS])+/g, function (all, t) {
        var v = map[t];
        if (v !== undefined) {
            if (all.length > 1) {
                v = "0" + v;
                v = v.substr(v.length - 2);
            }
            return v;
        } else if (t === "y") {
            return (date.getFullYear() + "").substr(4 - all.length);
        }
        return all;
    });
    /*返回自身*/
    return format;
}
// 时间格式转时间戳
Vue.prototype.$fixTime = (dateTime) => {
    dateTime = dateTime.replace(/(-)/g, '/'); // 把所有'-'转换为'/'
    return new Date(dateTime).getTime();
}
Vue.prototype.$popArray = (arr) => {
    var m = arr.slice();
    m.pop();
    return m;
},
Vue.prototype.$get = http.get
Vue.prototype.$post = http.post;

export default {
    
}


