// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import {
  router
} from './router/index'
import Ydui from 'vue-ydui'
import Axios from 'axios'
import qs from 'qs'
import echarts from 'echarts'
import 'vue-ydui/dist/ydui.rem.css'
import './assets/icon/iconfont.css'
import './assets/ydui.flexible'
import 'mint-ui/lib/style.css'
import '@/assets/base.css'
import App from './App'
import store from './store/store'
import tools from './bin/tools'
import {
  Cell,
  IndexList,
  IndexSection
} from 'mint-ui';
import {
  Alert,
  Toast
} from 'vue-ydui/dist/lib.rem/dialog';
import nodata from './components/common/nodata.vue';
import SelectButton from './components/common/selectButton.vue';

import gallery from 'img-vuer';
Vue.use(gallery)

// 解决移动端click事件300ms延迟
/* const FastClick = require('fastclick');

document.addEventListener('DOMContentLoaded', function () {
  FastClick.attach(document.body);
}, false); */

Vue.component(Cell.name, Cell);
Vue.component(IndexList.name, IndexList);
Vue.component(IndexSection.name, IndexSection);
Vue.component('no-data', nodata); // 注册全局暂无数据组件
Vue.component('select-button', SelectButton); // 注册全局选择按钮组件


Vue.use(Ydui)
// 使用百度echarts
Vue.prototype.$echarts = echarts
Vue.prototype.$axios = Axios
Vue.prototype.$qs = qs

Vue.config.silent = true // 取消所有日志和警告
Vue.config.productionTip = false
document.addEventListener("deviceready", function () {
  new Vue({
    el: '#app',
    router,
    store,
    components: {
      App
    },
    template: '<App @changeBaseUrl="changeBaseUrl"/>',
    methods: {
      // 配置axios基础服务器地址
      changeBaseUrl() {
        const serverip = window.localStorage.getItem("ip");
        const serverport = window.localStorage.getItem("port");
        const servername = window.localStorage.getItem("projectName");
        Axios.defaults.baseURL = `http://${serverip}:${serverport}/${servername}`
      }
    }
  });
  var backSecond = 0;
  document.addEventListener('backbutton', function () {
    var curSecond = new Date().getSeconds();
    if (Math.abs(curSecond - backSecond) > 2) {
      backSecond = curSecond;
      Toast({
        mes: '再点击一次退出APP!'
      });
    } else {
      navigator.app.exitApp();
    }
  }, false)
}, false);

