<template>
  <div id="app">
   <!--  <div class="replenish" v-if="showreplenish">
    </div> -->
    <transition :name=" showreplenish ? 'fade' : '' ">
      <keep-alive>
        <router-view @islogin="islogin" @changeDefaultPage="ischange" @changeServer="show1=true"></router-view>
      </keep-alive>
    </transition>
    <transition name="fade" v-if="this.$route.meta.showfoot">
      <yd-tabbar :fixed="true">
        <yd-tabbar-item title="首页" :link="{name:homeLink,query:{menuId:homeId}}" :class="this.$route.path.split('/')[1]==='home'?'router-link-active':''" v-if="$store.getters.getMenuConfig.includes('HOME')">
          <yd-icon slot="icon" name="home" style="font-size:.54rem" custom></yd-icon>
        </yd-tabbar-item>
        <yd-tabbar-item title="菜单" link="/menu" :class="this.$route.path.split('/')[1]==='menu'?'router-link-active':''" v-if="$store.getters.getMenuConfig.includes('MENU')">
          <yd-icon slot="icon" name="menu" style="font-size:.54rem" custom></yd-icon>
        </yd-tabbar-item>
        <yd-tabbar-item title="视频" link="/video" v-if="$store.getters.getMenuConfig.includes('VIDEO')">
          <yd-icon slot="icon" name="vedio" style="font-size:.54rem" custom></yd-icon>
        </yd-tabbar-item>
        <yd-tabbar-item title="警示" link="/alert" v-if="$store.getters.getMenuConfig.includes('CAUTION')">
          <yd-icon slot="icon" name="alert" style="font-size:.54rem" custom></yd-icon>
        </yd-tabbar-item>
      </yd-tabbar>
    </transition>
    <yd-popup v-model="show1" position="center" :close-on-masker="true" width="90%">
      <yd-cell-group>
        <yd-cell-item>
          <span slot="left">服务 I P ：</span>
          <yd-input slot="right" v-model="ipconfig" ref="ipconfig" required  placeholder="请输入ip"></yd-input>
        </yd-cell-item>
        <yd-cell-item>
          <span slot="left">服务端口：</span>
          <yd-input slot="right" v-model="portconfig" ref="portconfig" required regex="^([0-9]|[1-9]\d{1,3}|[1-5]\d{4}|6[0-5]{2}[0-3][0-5])$" placeholder="请输入端口号"></yd-input>
        </yd-cell-item>
        <yd-cell-item>
          <span slot="left">服务名称：</span>
          <yd-input slot="right" v-model="nameconfig" ref="nameconfig" required regex="^[a-zA-Z]+$" placeholder="请输入项目名称"></yd-input>
        </yd-cell-item>
        <yd-button type="warning" @click.native="checkIp" style="margin:10px">更改</yd-button>
      </yd-cell-group>
    </yd-popup>
  </div>
</template>

<script>
export default {
  name: "App",
  data() {
    return {
	//   show1:window.localStorage.getItem("ip")?false:true,
	  show1:false,
      ipconfig: "1.198.9.26",
      portconfig: "2256",
      nameconfig: "xldweb",
      logstate: false,
      homeLink: "",
      homeId:""
    };
  },
  computed: {
    showreplenish: function() {
      return !/(login)|(vpn)|(addressDetail)|(videoDetail)/.test(
        this.$route.name
      );
    }
  },
  methods: {
    checkIp() {
      const ip = this.$refs.ipconfig;
      const port = this.$refs.portconfig;
      const name = this.$refs.nameconfig;
      if (!ip.valid) {
        this.$dialog.toast({
          mes: "IP" + ip.errorMsg,
          timeout: 1000,
          icon: "error"
        });
        return;
      }
      if (!port.valid) {
        this.$dialog.toast({
          mes: "端口" + port.errorMsg,
          timeout: 1000,
          icon: "error"
        });
        return;
      }
      if (!name.valid) {
        this.$dialog.toast({
          mes: "名称" + name.errorMsg,
          timeout: 1000,
          icon: "error"
        });
        return;
      }
      window.localStorage.setItem("ip", this.ipconfig);
      window.localStorage.setItem("port", this.portconfig);
      window.localStorage.setItem("projectName", this.nameconfig);
      this.$emit("changeBaseUrl");
      this.show1 = false;
      // 修改后立即查询一次app名称
      this.$axios.get('/appuser/getAppInfo.action').then(res=>{
        this.$store.commit('changeAppTitle', res.data.data.appName)
        this.$store.commit('initDeviceBauth', res.data.data.deviceBauth)
        // this.$store.commit('initShowVideo', res.data.data.tvFunction)
      })
    },
    islogin() {
      //登录状态
      this.logstate = true;
      this.homeLink = "home" + this.$store.getters.defaultPageName;
      this.homeId = this.$store.getters.defaultPageId;
    },
    ischange() {
      this.homeLink = "home" + this.$store.getters.defaultPageName;
      this.homeId = this.$store.getters.defaultPageId;
    }
  },
  created() {
    if (window.localStorage.getItem("ip")) {
		this.$emit("changeBaseUrl");
		// this.ipconfig = window.localStorage.getItem('ip');
    	// this.portconfig = window.localStorage.getItem('port');
    	// this.nameconfig = window.localStorage.getItem('projectName');
    }
  }
};
</script>

<style>
#app {
  font-family: "Segoe UI Semilight", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  height: 100%;
  overflow: auto;
  font-size:.28rem;
}
#app button {
  font-family: "Segoe UI Semilight", Helvetica, Arial, sans-serif;
}
html,
body {
  height: 100%;
  /* background-color: #fff; */
}
header.yd-navbar {
  height: 1.45rem !important;
  padding-top: 0.45rem !important;
  /* margin-top: -0.45rem !important; */
}
.replenish {
  height: 1.45rem;
}
.yd-navbar:after {
  border-bottom: none !important;
}
.main-view {
  margin-bottom: 1.2rem;
}
/* .pop-out-enter-active,
.pop-out-leave-active,
.pop-in-enter-active,
.pop-in-leave-active {
  will-change: transform;
  transition: all .2s;
  height: 100%;
  top: 0;
  position: absolute;
  backface-visibility: hidden;
  perspective: 1000;
}

.pop-out-enter {
  opacity: 0;
  transform: translate3d(-100%, 0, 0);
}

.pop-out-leave-active {
  opacity: 0;
  transform: translate3d(100%, 0, 0);
}

.pop-in-enter {
  opacity: 0;
  transform: translate3d(100%, 0, 0);
}

.pop-in-leave-active {
  opacity: 0;
  transform: translate3d(-100%, 0, 0);
} */
.fade-enter-active,
.fade-enter-active {
  transition: all 0.2s ease-in;
}
.fade-enter-active {
  opacity: 1;
}
.fade-enter,
.fade-leave-active {
  opacity: 0;
}
.router-link-active {
  color: #2279ff !important;
}
.yd-cell-item:active::after {
  margin-left: 0.24rem;
  content: "";
  position: absolute;
  z-index: 0;
  bottom: 0;
  left: 0;
  width: 100%;
  border-bottom: 1px solid #d9d9d9;
  -webkit-transform: scaleY(0.5);
  transform: scaleY(0.5);
  -webkit-transform-origin: 0 0;
  transform-origin: 0 0;
}
.yd-cell-item::after {
  margin-left: 0.24rem;
  content: "";
  position: absolute;
  z-index: 0;
  bottom: 0;
  left: 0;
  width: 100%;
  border-bottom: 1px solid #d9d9d9;
  -webkit-transform: scaleY(0.5);
  transform: scaleY(0.5);
  -webkit-transform-origin: 0 0;
  transform-origin: 0 0;
}

</style>



