<template>
  <div class="select-group">
    <button v-for="(value,index) in data" :key="index" @click.stop="selected(value.value)" :class="{'active':isActive(value.value)}">{{value.name}}</button>
  </div>
</template>

<script>
  export default {
    props:{
      data:Array,
      type:{
        default:'radio'
      },
      value:{}
    },
    data(){
      return {
        selectData:null,
        selectValue:[]
      }
    },
    watch:{
      value:function(v){
        if(this.$props.type === 'radio'){
          if(v){
            this.selectValue = [v];
          }else{
            this.selectValue = [];
          }
        }
        else
        {
          if(v instanceof Array){
            this.selectValue = v;
          }else{
            this.selectValue = [];
          }
        }

      }
    },
    methods: {
      selected(index){
        if(this.$props.type === 'radio'){
          this.selectValue = [index];

        }else{
          let thisindex;
          this.selectValue.forEach((v,i) => {
            if(v === index){
              thisindex = i;
            }
          });
          if(thisindex || thisindex === 0){
            this.selectValue.splice(thisindex,1)
          }else{
            this.selectValue.push(index);
          }
        }
        this.$emit('select',this.selectValue)
      },
      isActive(index){
        if(this.$props.type === 'radio'){
          if(index === this.selectValue[0]){
            return true;
          }else{
            return false;
          }
        }else{
          if(this.selectValue.includes(index)){
            return true;
          }else{
            return false;
          }
        }
      }
    },
    created() {
      if(this.$props.type === 'radio'){
        if(this.$props.type)
        {
          this.selectValue = [this.$props.value];
        }
      }
      if(this.$props.value instanceof Array && this.$props.value.length > 0)
      {
        this.selectValue = this.$props.value;
      }
    },
  }
</script>

<style scoped>
  .select-group {
    display: flex;
    flex-wrap: wrap;
    padding: 0 .1rem;
  }
  .select-group>button {
    outline: none;
    border: 1px solid #ccc;
    border-radius: .1rem;
    word-break: break-all;
    padding: .05rem;
    width: 30%;
    font-size:.26rem;
    margin: .1rem;
  }
  button.active {
    border:1px solid #e61a0f;
  }
</style>




