import Vue from 'vue'
import Router from 'vue-router'
import { route } from './route'


Vue.use(Router)

export const router =  new Router(route)
// 路由导航守卫
let top = 0;
router.beforeEach((to, from, next) => {
  let localdata = window.sessionStorage.getItem('logindata');
  if (localdata && to.name === 'login') { // 判断是否已经登录且前往的是登录页
    next({
      name: 'home' + JSON.parse(localdata).userConfig.defaultPage.routeName
    });
  } else if (!localdata && to.name !== 'login') { // 判断是否已经登录且前往的不是登录页
    next({
      name: 'login'
    });
  }
  if (from.name === 'menu') {
    top = document.querySelector('#app').scrollTop;
  }
  next();
})
router.afterEach(route => {
  // 跳转menu页面时重新设置滚动距离
  let app = document.querySelector('#app')
  if(route.name === 'menu'){
    Vue.nextTick(function () {
      app.scrollTo(0,top);
    })
  }else{
    Vue.nextTick(function () {
      app.scrollTo(0, 0);
    })
  }
})




