import Axios from 'axios'
import qs from 'qs'
import { Alert, Loading } from 'vue-ydui/dist/lib.rem/dialog';

Axios.defaults.timeout = 10000; //响应时间
Axios.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded;charset=UTF-8';
const serverip = window.sessionStorage.getItem("ip");
const serverport = window.sessionStorage.getItem("port");
const servername = window.sessionStorage.getItem("projectName");
Axios.defaults.baseURL = `http://${serverip}:${serverport}/${servername}`
// Axios.interceptors.request.use(function (config) {
//   // 如果请求方式是post，则使用qs处理参数
//   if (config.method == 'post') {
//     config.data = qs.stringify(config.data)
//   }
//   return config;
// }, function (error) {
//   // 对请求错误做些什么
//   return Promise.reject(error);
// });

export default {
    get: (url, data, callback,msg) => {
        Loading.open(msg)
        Axios.get(url, { params: data }).then(res => {
            callback(res.data)
            Loading.close()
        }).catch(e => {
            Loading.close()
            console.log(e)
            Alert({ mes: '请求失败！' })
        })
    },
    post: (url, data, callback,msg) => {
        Loading.open(msg)
        Axios.post(url, qs.stringify(data)).then(res => {
            callback(res.data)
            Loading.close()
        }).catch(e => {
            Loading.close()
            console.log(e)
            Alert({ mes: '请求失败！' })
        })
    }
}


