import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    logindata: null,
    timeout: {
      riverTimeout: null,
    },
    appTitle: '',
    deviceBauth: 0,
  },
  getters: {
    getTeamId: state => {
      if (state.logindata) {
        return state.logindata.userConfig.userInfo.teams[0].teamId
      }
    },
    defaultPageName: state => {
      if (state.logindata) {
        return state.logindata.userConfig.defaultPage.routeName
      }
    },
    defaultPageId: state => {
      if (state.logindata) {
        return state.logindata.userConfig.defaultPage.menuId
      }
    },
    pageConfig: state => {
      if (state.logindata) {
        return state.logindata.userConfig.pageConfig;
      }
    },
    getMenuConfig: state => {
      if (state.logindata) {
		  var menu = state.logindata.userConfig.baseMenuConfigs;
		  var menuArray = [];
		  menu.forEach((v,i)=>{
			  menuArray.push(v.routename)
		  })
        return menuArray;
      }
    }
  },
  mutations: {
    datainit(state, data) {
      state.logindata = data;
      window.sessionStorage.setItem("logindata", JSON.stringify(data))
    },
    changeHomePage(state, obj) {
      state.logindata.userConfig.defaultPage.routeName = obj.name;
      state.logindata.userConfig.defaultPage.menuId = obj.id;
      window.sessionStorage.setItem("logindata", JSON.stringify(state.logindata))
    },
    dataout(state) {
      state.logindata = null;
      window.sessionStorage.setItem("logindata", "")
    },
    changeAppTitle(state, name) {
      state.appTitle = name;
    },
    initDeviceBauth(state, boolean) {
      state.deviceBauth = boolean;
    },
    initShowVideo(state, boolean) {
      state.showVideo = boolean;
    },
	  initUnit(state,data){
		  state.unitData = data;
	  },
    changeWaterType(state,type){
      state.waterType = type;
    }
  }
});



